// Inside university component
#university {
  li {
    > div > span {
      position: relative;
      bottom: 75px;
      left: 130px;
      a {
        text-decoration: none;
        color: red;
      }
    }
    padding: 18px;
    img {
      width: 123px;
      height: 70px;
      display:inline-block;
      border: 1px solid #d7d7d7;
    }
    border: 1px solid #d7d7d7;
    ul {
      margin-left: -4px;
      margin-right: -4px;
      li:first-of-type a,
      li:nth-of-type(2) {
        &:after {
          content: ',';
          color: black;
        }
      }
    }
    span {
      font-size: 13px;
    }
  }
  li:first-of-type {
    ul li:first-of-type a { 
      &:after {
        content: ',';
        color: #ffbf00;
      }
    }
  }
  p {
    display: inline-block;
    font-size: 14px;
    width: 370px;
    position: relative;
    bottom: 45px;
  }
  p:nth-of-type(2) {
    left: 130px;
  }
  div div {
    position: relative;
    bottom: 90px;
    left: 231px;
    ul {
      display: inline-block;
      li {
        display: inline-block;
        width: auto;
        height: auto;
        padding: 0;
        margin: 0;
        border: none;
        a { 
          text-decoration: none; 
          color: black;
          font-size: 14px;
        }
      }
    }
  }
}

