@import 'bourbon/bourbon';
main {
  &:after {

    content: "";
    display: table;
    clear: both;
  }
  padding: 40px 0 0 0;
  .devices {
    li {
      color: #fff;
      img {
        width: 100%;
      }
      div {
        line-height: 1.5;
        width: 194px;
        margin: 0 auto;
        box-sizing: border-box;
        padding-top: 5px;
        text-align: center;
        font-size: 14px;
        p:nth-of-type(1) {
          font-weight: lighter;
        }
        p:nth-of-type(2) {
          font-weight: 600;
        }
      }
      width: 222px;
      height: 261px;
      float: left;
      background-color: red;
      margin: 0 25px 0 0;
    }
  }
  #university,
  #news,
  #teamsandathletes,
  #community {
    // 548 by 262 px
    h1 {
      display: inline-block;
    }
    width: 548px;
    height: 264px;
    float:left;
    clear: both;
    overflow:hidden;
    margin-top: 42px;
    li {
      width: 100%;
      height: 112px;
      border: 1px solid black;
      box-sizing: border-box;
      background-color: #eeeeee;
    }
    li:nth-of-type(2) {
      margin-top: 16px;
    }
  }
  @import 'src/sass/components/main/_locator';
  @import 'src/sass/components/main/_university';
}

