   #locator {
    background-color: #f4f4f4;
    width: 383px;
    height: 261px;
    float: right;
    box-sizing: border-box;
    padding: 15px 8px 19px 19px;
    font-size: 14px; 

    h1 {
      display: inline;
      text-transform: uppercase;
      font-weight: 400;
      position: relative;
      bottom: 2px;
      font-size: 19px; 
    }
    
    p:first-of-type {
      display: block;
      align: left;
      margin-top: 22px;
      margin-bottom: 5px;
      font-size: 14px;
      line-height: 21px;
      color: black; 
    }

    .logo-icon {
      width: 26px;
      margin-left: 30px; 
    }

    input[type="text"] {
      width: 260px;
      height: 25px;
      margin-left: 5px;
      padding: 0 0 2px 10px;
      box-sizing: border-box;
      box-shadow: inset 0px 1px 0px 0px #E5E5E5; 
      &::placeholder {
        color: black;
      }
    }
  }

     

